<template>

<div class="product" v-for="data in productsData" :key="data">
  <div class="thumbnail" :style="{backgroundImage : `url(${data.image})`}"></div>
  <div class="product__border">
    <h5 class="title" @click="clickTitle(data.id)">{{ data.title }}</h5>
    <p class="date">{{data.date}}</p>
    <p class="price">{{data.price}}원</p>
    <p class="product__likes">☆{{ data.likes }}</p>
  </div>
</div>   

<!-- <div class="product__paging"><span @click="paging(num-1)" v-for="num in numbering_num" :key="num">[{{num}}]</span></div> -->
<!-- <div class="product__paging">총 게시물 : {{ allCount }}개</div> -->
</template>

<script>

export default {
  name : 'MarketPage',
  data() {
    return {
      productsData : [],
      start: null, 
      next: null, 
      size: 2,
      allCount : 0,
      numbering_num : 0,

    }
  },
  methods : {
    numbering(){
      var db = this.$store.state.db;
      db.collection('meta').doc('product').get()
      .then((result)=>{
        this.allCount = result.data().count;
        if(this.allCount % this.size == 0){
          this.numbering_num = this.allCount / this.size;
        }else{
          this.numbering_num = Math.round(this.allCount / this.size);
        }
        
      })
    },
    clickTitle(id){
    // window.location.href='/detail?id=' + id;
    this.$router.push('/detail?id=' + id);
  }  
  },
  beforeMount(){
    //this.numbering(); //총 게시물 개수

    //var i = 5; //5개까지만 들고옴
    var db = this.$store.state.db;
    db.collection('product').orderBy('date')
    //.limit(i)
    .get()
    .then((result)=>{
      result.forEach((doc) => {
        var data = doc.data();
        var addData = {
          date : data.date.toDate().toLocaleString(),
          detail : data.detail,
          image : data.image,
          name : data.name,
          price : data.price,
          title : data.title,
          uid : data.uid,
          likes : data.likes,
          id : doc.id
        }
        if(addData.image == ''){
          addData.image = 'https://firebasestorage.googleapis.com/v0/b/vue-project01-a89a2.appspot.com/o/image%2Fvecteezy_icon-image-not-found-vector_.jpg?alt=media&token=c01fbe73-5dde-426c-a2c0-e48962987df6'
        }
        this.productsData.push(addData);
        this.start = doc
      });
    })
  },
  
}
</script>

<style scoped>
  :root{
    --text-color:#f0f4f5;
    --background-color : #263343;
    --accent-color : #d49466;
    
  }
  .product {
    display: flex;
    padding: 10px;
    justify-content: center;
  }
  .thumbnail {
    max-width: 200px;
    width: 100%;
    border-radius: 10px;
    background-size: cover;
    background-position: center;
    max-height: 200px;
  }
  .product .price {
    font-size: 16px;
    font-weight: 600;
  }
  .product .title {
    font-size: 17px;
    font-weight: 600;
    cursor: pointer;
  }

  .product .title:hover{
    text-decoration: underline;
    text-underline-position: under;
  }
  .product .date {
    color: grey;
    font-size: 13px;
  }
  
  .product__likes{
    text-align: end;
    padding-left: 300px;
  }

  .product__border{
    padding-right: 40px;
    padding-left: 40px;
    width:500px;
    background: var(--text-color);
  }

  .product__paging{
    text-align: center;
    cursor: pointer;
    padding-top : 30px;
  }
@media screen and (max-width: 768px) {
  .product__likes{
  text-align: end;
  padding-left: 0px;
  }
}
</style>