<template>
  <div class="wrap">
  <div class="form_box">
    <h3 class="upload__title">UPLOAD</h3>
    <input class="input_field" v-model="title" type="text" placeholder="제목" ><br>
    <input class="input_field" v-model="price" type="number" min="0" placeholder="가격" ><br>
    <div class="input_field1_div">
      <input class="input_field1" v-model="productCount" type="number" min="1" max="9999" placeholder="수량" ><br>
      <input class="input_field1" :disabled="isBoxChecked" v-model="deliveryCharge" type="number" min="0" placeholder="배송비" ><br>
      <input class="input_field1_checkbox" type="checkbox" v-model="isCheck">무료배송
    </div>
    
    <textarea class="textarea_field" v-model="content" placeholder="내용을 입력해주세요."></textarea><br>
    <input class="input_field" id="fileInfo" type="file" accept=".jpg, .png, .jpeg">
    <button class="write_button" :disabled="btnUpload" @click="clickBtnUpload">업로드</button>
  </div>
</div>
</template>

<script>

export default {
  name : 'UploadProduct',
  data() {
    return {
      title : '',
      price : '',
      productCount : '',
      deliveryCharge : '',
      content : '',
      image : '',
      date : '',
      firebase : this.$store.state.firebase,
      db : this.$store.state.db,
      btnUpload : false,
      isCheck : false,
      isBoxChecked : false,
    }
  },
  beforeCreate () { 
    if (localStorage.getItem('user') == undefined) { 
      this.$router.push('/login')
      //현재 서버가 없기때문에 임시로 라이프사이클을 이용해 리다이렉트 처리
    } 
  },
  methods: {
    clickBtnUpload(){
      //업로드전에 사용자의 로그인상태 체크하는 코드 추가 필요
      //게시판 글 개수 카운트 + 1

      const storage = this.firebase.storage();
      this.btnUpload = true //업로드 완료될때까지 버튼 비활성화
      
      //null값 chk
      if(this.beforeUpload() == false){
        return false;
      }
      var file = document.querySelector('#fileInfo').files[0];

      var data={
        date : this.date,
        detail : this.content,
        image : this.image,
        likes : 0,
        name : JSON.parse(localStorage.getItem('user')).displayName,
        price : this.price,
        title : this.title,
        uid : JSON.parse(localStorage.getItem('user')).uid,
        productCount : this.productCount,
        deliveryCharge : this.deliveryCharge
      }

      if(file == undefined){
        //이미지파일이 없을때
        this.db.collection('product').add(data)
        .then(()=>{
          this.btnUpload = false; //버튼 비활성화 해제
          alert('성공적으로 저장되었습니다.');
          this.$router.push('/market')
        })
      }else{
        //이미지파일이 있을때
        var storageRef = storage.ref();
        var savePath = storageRef.child('image/' + this.image);
        var uploadFile = savePath.put(file);

        uploadFile.on('state_changed',
        //변화시 동작하는 함수
        null,
        //에러시 동작하는 함수
        (error)=>{
          console.error('실패사유는 ', error);
          this.btnUpload = false; //버튼 비활성화 해제
        },
        //성공시 동작하는 함수
        ()=>{
          uploadFile.snapshot.ref.getDownloadURL()
          .then((url)=>{
            //console.log('업로드된 경로는 ', url);
            data.image = url;
            this.db.collection('product').add(data)
            .then(()=>{
              this.btnUpload = false; //버튼 비활성화 해제
              alert('성공적으로 저장되었습니다.')
              this.$router.push('/market')
            })
            .catch((err)=>{
              alert('저장실패 : ' + err);
              this.btnUpload = false; //버튼 비활성화 해제
            })
          })
        })
      }

    },
    beforeUpload(){
      var file = document.querySelector('#fileInfo').files[0];
      if(this.title.trim() == '' || this.content.trim() == ''|| this.price == ''){
        alert('미입력된 항목이 있습니다');
        this.btnUpload = false; //버튼 비활성화 해제
        return false;
      }

      if( file != undefined){
        //파일이 있는경우 파일명 파싱
        var fileNameParsing = file.name.split('.');
        if(fileNameParsing[1] == 'jpg' || fileNameParsing[1] == 'png' || fileNameParsing[1] == 'jpeg'){
          var dateTime = new Date();
          var uniqueFileName = fileNameParsing[0] + dateTime.getTime() + '.' + fileNameParsing[1];
          this.image = uniqueFileName;
          this.date = dateTime;

          return true;
        }else{
          alert('jpg, png, jpeg 파일만 업로드 가능합니다')
          return false;
        }
      }else if( file == undefined){
        //파일이 없는경우 시간만 설정
        this.date = new Date();
        return true;
      }
    }
  },
  watch:{
    isCheck:function(value){
      if(value == true){
        this.deliveryCharge = 0;
        this.isBoxChecked = true;
      }else{
        this.deliveryCharge = '';
        this.isBoxChecked = false;
      }
    },
    price(value){
      if(value < 0){
        this.price = 0;
      }
    },
    productCount(value){
      if(value < 0){
        this.productCount = 1;
      }else if(value > 9999){
        this.productCount = 9999;
        alert('최대 수량은 9999입니다');
      }
    }
  }
}
</script>

<style scoped>
  :root{
    --text-color:#f0f4f5;
    --background-color : #263343;
    --accent-color : #d49466;
  }
  .wrap{
    display: flex;
    justify-content: center;
    padding-top: 50px;
  }
  .form_box {
    background-color: var(--text-color);
    margin: 10px;
    border-radius: 4px;
    border: 1px solid #ddd;
    padding: 10px;
    width: 700px;
    text-align: end;
}
.input_field {
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 4px;
    margin: 3px 0;
    font-size: 16px;
    width: 100%;
    font-family: 'NanumBarunGothic';
}
.input_field[type="file"]::file-selector-button{
  background-color: var(--background-color);
  border: 1px solid #323f6b;
  color: #ffffff;
  border-radius: 4px;
  padding: 5px 8px;
  font-family: 'NanumBarunGothic';
}
.textarea_field {
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 4px;
    margin: 3px 0;
    font-size: 16px;
    width: 100%;
    height: 160px;
    font-family: 'NanumBarunGothic';
}
.write_button {
  background-color: var(--background-color);
  border: 1px solid #323f6b;
  color: #ffffff;
  border-radius: 4px;
  padding: 5px 8px;
  font-size: 18px;
  font-family: 'NanumBarunGothic';
  
}
button:disabled{
  background: #3A4E66;
}
.upload__title{
  text-align: left;
}
.input_field1_div{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.input_field1{
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 4px;
  margin: 3px 0;
  font-size: 16px;
  font-family: 'NanumBarunGothic';
  width:40%;
}
.input_field1_checkbox{
  width:15px;
  height: 15px; 
}
@media screen and (max-width: 768px) {
  .input_field1{
    width:35%;
  }
}
</style>